.news {
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 40px;
    width: 100%;
    max-width: 1250px;
  }

  &__select {
    width: 180px;
  }

  &__search {
    width: 730px;
  }

  &__featured-button {
    padding: 10px 20px;
    text-decoration: none;
    text-transform: uppercase;
    color: white;
    border: 1px solid white;
    border-radius: 5px;
    transition: all 0.2s;

    &:hover {
      text-decoration: none;
      background-color: rgba(25, 118, 210, 0.04);
      border: 1px solid #1976d2;
    }
  }
}
